<template>
    <div class="careerAnchor">
        <masks v-show="jinqitanchuang||xuekaotanchuang||xueqitanchuang||gaokaotanchuang||false"></masks>
        <div class="careerAnchorBox ">
            <!-- <img src="imgs/careerManagement/careerAnchor/banner.png" alt="" class="banner"> -->
            <div class="parts" style="padding-bottom: 20px;">
                <div class="top" style="padding-bottom:50px">
                    <div class="title">
                        <img src="imgs/careerArchives/icon_biaoti.png" alt="">
                        目标概览
                    </div>
                </div>
                <div class="gaokaomubiao" v-if="tipsView&&tipsView.gao">
                    <div class="box" v-for="(item, index) in tipsView.gao" :key="index" v-if="index < 3">
                        <p class="title">高考目标{{index + 1}}</p>
                        <template v-if="item.totalScore || item.rank">
                            <p class="detail">
                                <span>总分：<template v-if="item.totalScore">{{item.totalScore}}</template><template v-else>--</template></span>
                            </p>
                            <p ><span>本校排名：<template v-if="item.rank">{{item.rank}}</template><template v-else>--</template></span></p>
                       </template>    
                        <template v-else>~ ~暂无内容</template>
                    </div>
                </div>
                <div class="echartsBox">
                    <div id="echarts" style="width: 938px;height:300px;"></div>
                </div>
                <div class="bottomBox" v-if="tipsView&&tipsView.total">
                    <p class="jinqi">
                        <template v-if="tipsView.total">
                            <span>近期目标共：{{tipsView.total}}</span>
                            <span>已完成：{{tipsView.totalFinish}}个</span>
                            <span>未完成：{{tipsView.totalUnFinish}}个</span>
                        </template>
                        <template v-else>
                           近期目标 暂无内容~~
                        </template>
                    </p>
                    <p class="benyuejinqi">
                        <template v-if="tipsView.current">
                            <span>本月近期目标共：{{tipsView.current}}</span>
                            <span>已完成：{{tipsView.currentFinish}}个</span>
                            <span>未完成：{{tipsView.currentUnFinish}}个</span>
                        </template>
                        <template v-else>
                           本月近期目标 暂无内容~~
                        </template>
                    </p>
                </div>
                <!-- <div class="noneContent" v-if="GgaokaoList.length==0"></div> -->
            </div>
            <div class="parts">
                <div class="top" style="padding-bottom:50px">
                    <div class="title">
                        <img src="imgs/careerArchives/icon_biaoti.png" alt="">
                        高考目标
                    </div>
                    <p class="tips">最多添加3个高考目标！</p>
                    <span @click="gaokaotanchuang=true" style="position: relative;top:20px">新增</span>
                </div>
                <div class="tables">
                    <table>
                        <tr v-for="(item,index) in GgaokaoList">
                            <td>高考目标{{index+1}}</td>
                            <td>
                                <span>总分</span>
                               <template v-if="item.totalScore != null">{{item.totalScore}}</template>
                               <template v-else>--</template>
                            </td>
                            <td>
                                <span>全省排名</span>
                                {{item.rank||'--'}}
                            </td>
                            <td v-for="(n,key)  in item.list">
                                <span>{{n.name}}</span>
                                {{n.score}}
                            </td>
                            <td>
                                <span>日期</span>
                                {{item.updatedAt||'--'}}
                            </td>
                            <td>
                                <span>操作</span>
                                <i class="icon" @click="gaokaoShow(item.id)"></i>
                            </td>
                        </tr>
                    </table>
                </div>
                <div class="noneContent" v-if="GgaokaoList.length==0"></div>
            </div>
            <div class="parts">
                <div class="top" >
                    <div class="title">
                        <img src="imgs/careerArchives/icon_biaoti.png" alt="">
                        学期目标
                    </div>
                </div>
                <div class="tables">
                    <table>
                        <tr v-for="(item,index) in xueqitTotal">
                            <td>{{item.title}}</td>
                            <td>
                                <span>总分</span>
                                {{item.totalScore||'--'}}
                            </td>
                            <td>
                                <span>本校排名</span>
                                {{item.rank||'--'}}
                            </td>
                            <td v-for="(n)  in item.list||subjectList">
                                <span>{{n.name||n.subject}}</span>
                                {{n.score||'--'}}
                            </td>
                            <td>
                                <span>日期</span>
                                {{item.updatedAt||'--'}}
                            </td>
                            <td>
                                <span>操作</span>
                                <i class="icon" @click="xueqiShow(item.id),xueqiType=(index+1)"></i>
                            </td>
                        </tr>
                    </table>
                </div>
            </div>
            <div class="parts">
                <div class="top" >
                    <div class="title">
                        <img src="imgs/careerArchives/icon_biaoti.png" alt="">
                        学考目标
                    </div>
                    <span @click="xuekaotanchuang=true" style="position: relative;top:20px">编辑</span>
                </div>
                <div class="tables2" style="padding-top: 60px" >
                    <table>
                        <tr>
                            <td style="width: 120px">科目</td>
                            <td style="width: 210px">学考目标</td>
                            <td>备注</td>
                        </tr>
                        <tr v-for="item in subjectList">
                            <td>{{item.subject}}</td>
                            <td>
                                <div class="aims">
                                    <p>
                                        等级： 
                                        <template v-if="item.level != null && item.level != ''">{{item.level}}</template>
                                        <template v-else>--</template>
                                    </p>
                                    <p>
                                        分数： 
                                        <template v-if="item.score != null && item.score != ''">{{item.score}}</template>
                                        <template v-else>--</template>
                                    </p>
                                </div>
                            </td>
                            <td style="text-align: left;padding-left: 10px;">{{item.remark}}</td>
                        </tr>
                    </table>
                </div>
            </div>
            <div class="parts">
                <div class="top" style="padding-bottom:50px">
                    <div class="title">
                        <img src="imgs/careerArchives/icon_biaoti.png" alt="">
                        近期目标
                    </div>
                    <span @click="jinqitanchuang=true" style="position: relative;top:20px">新增</span>
                </div>
                <div class="tables2" style="padding-top: 20px" >
                    <table>
                        <tr>
                            <td>优先等级</td>
                            <td>项目</td>
                            <td>目标内容</td>
                            <td>完成期限</td>
                            <td>达成情况</td>
                            <td>删除</td>
                            <td>编辑</td>
                        </tr>
                        <tr v-for="(item,index) in jinqiData">
                            <td>{{(currentPage-1)*pageSize+index+1}}</td>
                            <td>
                                <template v-if="item.furtherWay||item.competition">{{item.furtherWay}}&ensp;&ensp;{{item.competition}}</template>
                                <template v-else>--</template>
                            </td>
                            <td>
                                {{item.target}}
                            </td>
                            <td>
                                {{item.startDate+'至'+item.endDate}}
                            </td>
                            <td>
                                <span class="finshed" @click="jinqiFinshe(item.id)" v-if="item.isOver==2">未完成</span>
                                <img src="imgs/careerManagement/decompose/wancheng.png" alt="" v-if="item.isOver==1">
                            </td>
                            <td>
                                <i class="icon icon2" @click="jinqidel(item.id)"></i>
                            </td>
                            <td>
                                <i class="icon" @click="showjinqi(item.id)"></i>
                            </td>
                        </tr>
                    </table>
                </div>
                <el-pagination v-if="jinqiData.length!=0"
                               background
                               @current-change="currentChange"
                               :page-size="pageSize"
                               layout="prev, pager, next"
                               :total="total"
                               class="pages"
                >
                </el-pagination>
                <div class="noneContent" v-if="jinqiData.length==0"></div>
            </div>
        </div>
        <div class="Cards" v-show="gaokaotanchuang">
            <div class="header">高考目标</div>
            <ul class="inputList">
                <li>
                    <p>总分:</p>
                    <input type="number" v-model="GtotalScore">
                </li>
                <li>
                    <p>全省排名:</p>
                    <input type="number" v-model="Grank">
                </li>
                <li></li>
                <li></li>
                <li>
                    <p>语文:</p>
                    <input type="number" v-model="GsubjectLanguage">
                </li>
                <li>
                    <p>数学:</p>
                    <input type="number" v-model="GsubjectMath">
                </li>
                <li>
                    <p>外语:</p>
                    <input type="number" v-model="GsubjectForeignLanguage">
                </li>
                <li>
                    <p>物理:</p>
                    <input type="number" v-model="GsubjectPhysics">
                </li>
                <li>
                    <p>化学:</p>
                    <input type="number" v-model="GsubjectChemistry">
                </li>
                <li>
                    <p>生物:</p>
                    <input type="number" v-model="GsubjectBiology">
                </li>
                <li>
                    <p>思想政治:</p>
                    <input type="number" v-model="GsubjectIdeologicalPolitics">
                </li>
                <li>
                    <p>历史:</p>
                    <input type="number" v-model="GsubjectHistory">
                </li>
                <li>
                    <p>地理:</p>
                    <input type="number" v-model="GsubjectGeography">
                </li>
                <li>
                    <p>技术:</p>
                    <input type="number" v-model="GsubjectTechnology">
                </li>
            </ul>
            <div class="save" @click="gaokaoAddEdit(),clearGao()">
                保存
            </div>
            <div class="close" @click="gaokaotanchuang=false,clearGao()">
                ×
            </div>
        </div>
        <div class="Cards Xueqi" v-show="xueqitanchuang">
            <div class="header">学期目标</div>
            <ul class="inputList">
                <li>
                    <p>总分:</p>
                    <input type="number" v-model="GtotalScore">
                </li>
                <li>
                    <p>本校排名:</p>
                    <input type="number" v-model="Grank">
                </li>
                <li></li>
                <li></li>
                <li>
                    <p>语文:</p>
                    <input type="number" v-model="GsubjectLanguage">
                </li>
                <li>
                    <p>数学:</p>
                    <input type="number" v-model="GsubjectMath">
                </li>
                <li>
                    <p>外语:</p>
                    <input type="number" v-model="GsubjectForeignLanguage">
                </li>
                <li>
                    <p>物理:</p>
                    <input type="number" v-model="GsubjectPhysics">
                </li>
                <li>
                    <p>化学:</p>
                    <input type="number" v-model="GsubjectChemistry">
                </li>
                <li>
                    <p>生物:</p>
                    <input type="number" v-model="GsubjectBiology">
                </li>
                <li>
                    <p>思想政治:</p>
                    <input type="number" v-model="GsubjectIdeologicalPolitics">
                </li>
                <li>
                    <p>历史:</p>
                    <input type="number" v-model="GsubjectHistory">
                </li>
                <li>
                    <p>地理:</p>
                    <input type="number" v-model="GsubjectGeography">
                </li>
                <li>
                    <p>技术:</p>
                    <input type="number" v-model="GsubjectTechnology">
                </li>
            </ul>
            <div class="save" @click="xueqiAdd(),clearGao()">
                保存
            </div>
            <div class="close" @click="xueqitanchuang=false,clearGao()">
                ×
            </div>
        </div>
        <div class="Cards Xuekao" v-show="xuekaotanchuang">
            <div class="header">学考目标</div>
            <div class="tables2">
                <table>
                    <tr>
                        <td>科目</td>
                        <td style="width: 30%">学考目标</td>
                        <td>备注</td>
                    </tr>
                    <tr v-for="item in subjectList">
                        <td>{{item.subject}}</td>
                        <td>
                            <!-- <input type="text" v-model="item.target"> -->
                            <div class="aimsBox">
                                等级：
                                <el-select  v-model="item.level" placeholder="">
                                    <el-option  :label="itemd" :value="itemd" v-for="(itemd,index) in grade" :key="index"></el-option>
                                </el-select>
                            </div>
                            <div class="aimsBox">
                                分数：<input type="text" v-model="item.score">
                            </div>
                        </td>
                        <td><input type="text" v-model="item.remark"  maxlength="30" style="width: 100%"></td>
                    </tr>
                </table>
            </div>
            <div class="save" @click="xueKaoAdd(),clearGao()">
                保存
            </div>
            <div class="close" @click="xuekaotanchuang=false,clearGao()">
                ×
            </div>
        </div>
        <div class="Cards Jinqi" v-show="jinqitanchuang">
            <div class="header">近期目标</div>
            <div class="jinqiBox">
                <div class="smallBox">
                    <p>优&ensp;先&ensp;级：</p>
                    <el-select  v-model="jinqiSort" placeholder="请选择优先级">
                        <el-option  label="最前" value="0"></el-option>
                        <el-option  label="最后" value="最后"></el-option>
                    </el-select>
                </div>
                <div class="smallBox">
                    <p>项&emsp;&emsp;目：</p>
                    <div class="box">
                        <el-select  v-model="jinqiFurtherWay" placeholder="本项选填">
                            <el-option  :label="item" :value="item" v-for="(item, index) in furtherWayList" :key="index"></el-option>
                        </el-select>
                    </div>
                    <div class="box">
                        <el-select  v-model="jinqiCompetition" placeholder="本项选填">
                            <!-- <el-option  label="--" value=""></el-option> -->
                            <el-option  label="竞赛" value="竞赛"></el-option>
                        </el-select>
                    </div>
                </div>
                <div class="smallBox">
                    <p>内&emsp;&emsp;容：</p>
                    <textarea  v-model="jinqiText" placeholder="此处最多可输入30个字"></textarea>
                </div>
                <div class="block smallBox">
                    <p>完成日期：</p>
                    <el-date-picker
                            v-model="jinqiDate"
                            type="daterange"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            value-format="yyyy-MM-dd">
                    </el-date-picker>
                </div>
            </div>
            <div class="save" @click="jinqiAdd(),clearGao()" >
                保存
            </div>
            <div class="close" @click="jinqitanchuang=false,clearGao()">
                ×
            </div>
        </div>
    </div>
</template>

<script>
    import  masks from '../../components/mask.vue'
    export default {
        name: "decompose",
        components:{masks},
        data(){
            return {
                gaokaoTotalScore: [0, 0, 0, 0, 0],
                gaokaoRank: [0, 0, 0, 0, 0],
                gaokaotanchuang:false,
                xueqitanchuang:false,
                xuekaotanchuang:false,
                jinqitanchuang:false,
                // 目标概览
                tipsView:null,
                //高考目标
                GgaokaoList:[],
                GgaokaoId:'',
                GtotalScore:'',//总分
                Grank:'',//名次
                GsubjectLanguage:'',//语文分数
                GsubjectMath:'',//数学分数
                GsubjectForeignLanguage:'',//外语分数
                GsubjectPhysics:'',//物理分数
                GsubjectChemistry:'',//化学分数
                GsubjectBiology:'',//生物分数
                GsubjectIdeologicalPolitics:'',//思想政治
                GsubjectHistory:'',//历史分数
                GsubjectGeography:'',//地理分数
                GsubjectTechnology:'',//技术
                //学期目标
                xueqiType:1,
                xueqitTotal:[
                    {title :'高一上(期中)'},
                    {title :'高一上(期末)'},
                    {title :'高一下(期中)'},
                    {title :'高一下(期末)'},
                    {title :'高二上(期中)'},
                    {title :'高二上(期末)'},
                    {title :'高二下(期中)'},
                    {title :'高二下(期末)'},
                    {title :'高三上(期中)'},
                    {title :'高三上(期末)'},
                    {title :'高三下(期中)'}
                ],
                subjectList:[
                    {
                        subject:'语文',
                        target:'',
                        remark:'',
                        level: '',
                        score: ''
                    },
                    {
                        subject:'数学',
                        target:'',
                        remark:'',
                        level: '',
                        score: ''
                    },
                    {
                        subject:'外语',
                        target:'',
                        remark:'',
                        level: '',
                        score: ''
                    },
                    {
                        subject:'物理',
                        target:'',
                        remark:'',
                        level: '',
                        score: ''
                    },
                    {
                        subject:'化学',
                        target:'',
                        remark:'',
                        level: '',
                        score: ''
                    },
                    {
                        subject:'生物',
                        target:'',
                        remark:'',
                        level: '',
                        score: ''
                    },
                    {
                        subject:'思想政治',
                        target:'',
                        remark:'',
                        level: '',
                        score: ''
                    },
                    {
                        subject:'历史',
                        target:'',
                        remark:'',
                        level: '',
                        score: ''
                    },
                    {
                        subject:'地理',
                        target:'',
                        remark:'',
                        level: '',
                        score: ''
                    },
                    {
                        subject:'技术',
                        target:'',
                        remark:'',
                        level: '',
                        score: ''
                    }
                ],
                xueqiListData:[],
                grade:['A','B','C','D','E'],
                //近期目标
                jinqiData:[],
                jinqiSort:'',
                jinqiFurtherWay: '',
                jinqiCompetition: '',
                jinqiText:'',
                jinqiDate:'',
                jinqiID:'',
                // 近期目标中的升学途径
                furtherWayList: [],
                //近期目标分页
                pageNumber:1,//当前页码
                pageSize:5,//每页大小
                total:0,//总条数,
                currentPage:1
            }
        },
        watch: {
            tipsView() {
                let arr = this.tipsView.xue;
                if(arr.length){
                    arr.forEach((item, index) => {
                        if(item.type == 2) {
                            this.gaokaoTotalScore[0] = item.totalScore;
                            this.gaokaoRank[0] = item.rank;
                        }else if(item.type == 4) {
                            this.gaokaoTotalScore[1] = item.totalScore;
                            this.gaokaoRank[1] = item.rank;
                        }else if(item.type == 6) {
                            this.gaokaoTotalScore[2] = item.totalScore;
                            this.gaokaoRank[2] = item.rank;
                        }else if(item.type == 8) {
                            this.gaokaoTotalScore[3] = item.totalScore;
                            this.gaokaoRank[3] = item.rank;
                        }else if(item.type == 10) {
                            this.gaokaoTotalScore[4] = item.totalScore;
                            this.gaokaoRank[4] = item.rank;
                        }
                    })

                    this.$nextTick(() => {
                        this.createEcharts('echarts',this.gaokaoTotalScore,this.gaokaoRank);
                    })
                }else {
                    this.$nextTick(() => {
                        this.createEcharts('echarts',this.gaokaoTotalScore,this.gaokaoRank);
                    })
                } 
            }
        },
        methods:{
            // 目标概览
            getGoalsOverview(){
                let _this=this
                this.$ajax.post(this.G_uri + '/targetDecompose/getGoalsOverview',{
                },{
                    headers:{
                        token: window.sessionStorage.getItem('ymtxToken')
                    }
                }).then((res)=>{
                    if(res.data.code==2000){
                        _this.tipsView=res.data.data;


                    }
                })
            },


            //高考目标添加&编辑
            gaokaoAddEdit(){
                let _this=this
                if(!_this.GgaokaoId){
                    this.$ajax.post(this.G_uri+this.ports.careerManagement.gaoGoal.add,{
                        totalScore:this.GtotalScore,
                        rank:this.Grank,
                        subjectLanguage:this.GsubjectLanguage,
                        subjectMath:this.GsubjectMath,
                        subjectForeignLanguage:this.GsubjectForeignLanguage,
                        subjectPhysics:this.GsubjectPhysics,
                        subjectChemistry:this.GsubjectChemistry,
                        subjectBiology:this.GsubjectBiology,
                        subjectIdeologicalPolitics:this.GsubjectIdeologicalPolitics,
                        subjectHistory:this.GsubjectHistory,
                        subjectGeography:this.GsubjectGeography,
                        subjectTechnology:this.GsubjectTechnology
                    },{
                        headers:{
                            token: window.sessionStorage.getItem('ymtxToken')
                        }
                    }).then((res)=>{
                        if(res.data.code==2000){
                            _this.gaokaoList();
                            _this.gaokaotanchuang=false;
                        }
                    })
                }else{
                    this.$ajax.post(this.G_uri+this.ports.careerManagement.gaoGoal.edit,{
                        id:this.GgaokaoId,
                        totalScore:this.GtotalScore,
                        rank:this.Grank,
                        subjectLanguage:this.GsubjectLanguage,
                        subjectMath:this.GsubjectMath,
                        subjectForeignLanguage:this.GsubjectForeignLanguage,
                        subjectPhysics:this.GsubjectPhysics,
                        subjectChemistry:this.GsubjectChemistry,
                        subjectBiology:this.GsubjectBiology,
                        subjectIdeologicalPolitics:this.GsubjectIdeologicalPolitics,
                        subjectHistory:this.GsubjectHistory,
                        subjectGeography:this.GsubjectGeography,
                        subjectTechnology:this.GsubjectTechnology
                    },{
                        headers:{
                            token: window.sessionStorage.getItem('ymtxToken')
                        }
                    }).then((res)=>{
                        if(res.data.code==2000){
                            _this.gaokaoList();
                            _this.gaokaotanchuang=false;
                        }
                    })
                }
            },
            //高考目标添加列表
            gaokaoList(){
                let _this=this
                this.$ajax.post(this.G_uri+this.ports.careerManagement.gaoGoal.list,{
                },{
                    headers:{
                        token: window.sessionStorage.getItem('ymtxToken')
                    }
                }).then((res)=>{
                    if(res.data.code==2000){
                        _this.GgaokaoList=res.data.data.list;
                    }
                })
            },
            //高考目标详情
            gaokaoShow(id){
                let _this=this
                this.$ajax.post(this.G_uri+this.ports.careerManagement.gaoGoal.show,{
                    id:id
                },{
                    headers:{
                        token: window.sessionStorage.getItem('ymtxToken')
                    }
                }).then((res)=>{
                    if(res.data.code==2000){
                        let data=res.data.data;
                        _this.GtotalScore=data.totalScore;
                        _this.Grank=data.rank;
                        _this.GsubjectLanguage=data.subjectLanguage;
                        _this.GsubjectMath=data.subjectMath;
                        _this.GsubjectForeignLanguage=data.subjectForeignLanguage;
                        _this.GsubjectPhysics=data.subjectPhysics;
                        _this.GsubjectChemistry=data.subjectChemistry;
                        _this.GsubjectBiology=data.subjectBiology;
                        _this.GsubjectIdeologicalPolitics=data.subjectIdeologicalPolitics;
                        _this.GsubjectHistory=data.subjectHistory;
                        _this.GsubjectGeography=data.subjectGeography;
                        _this.GsubjectTechnology=data.subjectTechnology;
                        _this.GgaokaoId=data.id;
                        _this.gaokaotanchuang=true;
                    }
                })
            },
            //清空高考目标
            clearGao(){
                this.GtotalScore='';
                this.Grank='';
                this.GsubjectLanguage='';
                this.GsubjectMath='';
                this.GsubjectForeignLanguage='';
                this.GsubjectPhysics='';
                this.GsubjectChemistry='';
                this.GsubjectBiology='';
                this.GsubjectIdeologicalPolitics='';
                this.GsubjectHistory='';
                this.GsubjectGeography='';
                this.GsubjectTechnology='';
                this.GgaokaoId='';
                this.jinqiSort='';
                this.jinqiFurtherWay='';
                this.jinqiCompetition='';
                this.jinqiText='';
                this.jinqiDate='';
                this.jinqiID=''
            },
            //添加学期目标
            xueqiAdd(){
                let _this=this;
                this.$ajax.post(this.G_uri+this.ports.careerManagement.xueQiGoal.addEidt,{
                    type:this.xueqiType,
                    totalScore:this.GtotalScore,
                    rank:this.Grank,
                    subjectLanguage:this.GsubjectLanguage,
                    subjectMath:this.GsubjectMath,
                    subjectForeignLanguage:this.GsubjectForeignLanguage,
                    subjectPhysics:this.GsubjectPhysics,
                    subjectChemistry:this.GsubjectChemistry,
                    subjectBiology:this.GsubjectBiology,
                    subjectIdeologicalPolitics:this.GsubjectIdeologicalPolitics,
                    subjectHistory:this.GsubjectHistory,
                    subjectGeography:this.GsubjectGeography,
                    subjectTechnology:this.GsubjectTechnology
                },{
                    headers:{
                        token: window.sessionStorage.getItem('ymtxToken')
                    }
                }).then((res)=>{
                    if(res.data.code==2000){
                        _this.xueqiList();
                        _this.getGoalsOverview();
                        _this.xueqitanchuang=false;
                    }
                })
            },
            //学期目标详情
            xueqiShow(id){
                if(id){
                    let _this=this;
                    this.$ajax.post(this.G_uri+this.ports.careerManagement.xueQiGoal.show,{
                        id:id
                    },{
                        headers:{
                            token: window.sessionStorage.getItem('ymtxToken')
                        }
                    }).then((res)=>{
                        if(res.data.code==2000){
                            let data=res.data.data;
                            _this.GtotalScore=data.totalScore;
                            _this.Grank=data.rank;
                            _this.GsubjectLanguage=data.subjectLanguage;
                            _this.GsubjectMath=data.subjectMath;
                            _this.GsubjectForeignLanguage=data.subjectForeignLanguage;
                            _this.GsubjectPhysics=data.subjectPhysics;
                            _this.GsubjectChemistry=data.subjectChemistry;
                            _this.GsubjectBiology=data.subjectBiology;
                            _this.GsubjectIdeologicalPolitics=data.subjectIdeologicalPolitics;
                            _this.GsubjectHistory=data.subjectHistory;
                            _this.GsubjectGeography=data.subjectGeography;
                            _this.GsubjectTechnology=data.subjectTechnology;
                            _this.GgaokaoId=data.id;
                            _this.xueqitanchuang=true;
                        }
                    })
                }else{
                    this.xueqitanchuang=true;
                }
            },
            //学期目标列表
            xueqiList(){
                let _this=this
                this.$ajax.post(this.G_uri+this.ports.careerManagement.xueQiGoal.list,{
                },{
                    headers:{
                        token: window.sessionStorage.getItem('ymtxToken')
                    }
                }).then((res)=>{
                    if(res.data.code==2000){
                          let data=res.data.data.list;
                            _this.xueqitTotal.forEach((item,index)=>{
                                data.forEach((n,i)=>{
                                    if(n.type==(index+1)){
                                        item.totalScore=n.totalScore;
                                        item.rank=n.rank;
                                        item.updatedAt=n.updatedAt;
                                        item.list=n.list;
                                        item.type=n.type;
                                        item.id=n.id
                                    }
                                })
                            })
                        _this.xueqitTotal=[..._this.xueqitTotal]
                    }
                })
            },
            //学考目标添加
            xueKaoAdd(){
                let _this=this;
                this.$ajax.post(this.G_uri+this.ports.careerManagement.xueKaoGoal.addEidt,{
                    subject:_this.subjectList
                },{
                    headers:{
                        token: window.sessionStorage.getItem('ymtxToken')
                    }
                }).then((res)=>{
                    if(res.data.code==2000){
                        // _this.xueqiList();
                        _this.xuekaotanchuang=false;
                        _this.xueKaoList();
                    }
                })
            },
            //学考目标列表
            xueKaoList(){
                let _this=this;
                this.$ajax.post(this.G_uri+this.ports.careerManagement.xueKaoGoal.list,{
                    subject:_this.subjectList
                },{
                    headers:{
                        token: window.sessionStorage.getItem('ymtxToken')
                    }
                }).then((res)=>{
                    if(res.data.code==2000){
                        // _this.xueqiList();
                        let data=res.data.data;
                        if(data.length>0){
                            _this.subjectList=data;
                        }
                        _this.xuekaotanchuang=false;
                    }
                })
            },
            //近期目标添加修改
            jinqiAdd(){
                let _this=this;
                if(!this.jinqiID){
                    //添加
                    this.$ajax.post(this.G_uri+this.ports.careerManagement.nearGoal.add,{
                        sort:this.jinqiSort,
                        furtherWay: this.jinqiFurtherWay,
                        competition: this.jinqiCompetition,
                        target:this.jinqiText,
                        startDate:this.jinqiDate[0],
                        endDate:this.jinqiDate[1]
                    },{
                        headers:{
                            token: window.sessionStorage.getItem('ymtxToken')
                        }
                    }).then((res)=>{
                        if(res.data.code==2000){
                            _this.jinqitanchuang=false;
                            _this.jinqiList(1,_this.pageSize);
                        }
                    })
                }else{
                    //修改
                    this.$ajax.post(this.G_uri+this.ports.careerManagement.nearGoal.edit,{
                        id:this.jinqiID,
                        sort:this.jinqiSort,
                        target:this.jinqiText,
                        startDate:this.jinqiDate[0],
                        endDate:this.jinqiDate[1]
                    },{
                        headers:{
                            token: window.sessionStorage.getItem('ymtxToken')
                        }
                    }).then((res)=>{
                        if(res.data.code==2000){
                            _this.jinqitanchuang=false;
                            _this.jinqiList(1,_this.pageSize);
                        }
                    })
                }
            },
            //近期目标列表
            jinqiList(pageNumber,pageSize){
                let _this=this;
                this.$ajax.post(this.G_uri+this.ports.careerManagement.nearGoal.list,{
                    pageNumber:pageNumber||1,
                    pageSize:pageSize||10
                },{
                    headers:{
                        token: window.sessionStorage.getItem('ymtxToken')
                    }
                }).then((res)=>{
                    if(res.data.code==2000){
                        _this.total=res.data.data.page.count;
                        _this.jinqiData=res.data.data.list
                    }
                })
            },
            //点击完成{
            jinqiFinshe(id){
                let _this=this;
                this.$ajax.post(this.G_uri+this.ports.careerManagement.nearGoal.complete,{
                    id:id
                },{
                    headers:{
                        token: window.sessionStorage.getItem('ymtxToken')
                    }
                }).then((res)=>{
                    if(res.data.code==2000){
                        _this.jinqiList(1,_this.pageSize);
                    }
                })
            },
            //删除近期目标
            jinqidel(id){
                let _this=this;
                this.$ajax.post(this.G_uri+this.ports.careerManagement.nearGoal.del,{
                    id:id
                },{
                    headers:{
                        token: window.sessionStorage.getItem('ymtxToken')
                    }
                }).then((res)=>{
                    if(res.data.code==2000){
                        this.jinqiList(1,_this.pageSize);
                    }
                })
            },
            //查看近期目标
            showjinqi(id){
                let _this=this;
                this.$ajax.post(this.G_uri+this.ports.careerManagement.nearGoal.show,{
                    id:id
                },{
                    headers:{
                        token: window.sessionStorage.getItem('ymtxToken')
                    }
                }).then((res)=>{
                    if(res.data.code==2000){
                        let data=res.data.data;
                        _this.jinqiText=data.target;
                        _this.jinqiDate=[data.startDate,data.endDate];
                        _this.jinqiID=data.id;
                        _this.jinqitanchuang=true;
                    }
                })
            },
            //近期目标中的升学途径
            jinqiFurtherWayList(){
                let _this=this;
                this.$ajax.post(this.G_uri + '/targetSetting/getFurtherWayList',{
                },{
                    headers:{
                        token: window.sessionStorage.getItem('ymtxToken')
                    }
                }).then((res)=>{
                    if(res.data.code==2000){
                        _this.furtherWayList = res.data.data;
                    }
                })
            },
            //当近期页面变化
            currentChange(num) {
                this.currentPage = num;
                this.jinqiList(num,this.pageSize);
            },
            createEcharts(el, data1, data2) {
                var el = el;
                var data1 = data1;
                var data2 = data2;
                // 基于准备好的dom，初始化echarts实例
                var myChart = this.$echarts.init(document.getElementById(el));
                // 指定图表的配置项和数据
                var option = {
                    title: {
                        text: '学期目标',
                        subtext: '',
                        color: '#3492f9'
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        data: ['成绩', '排名']
                    },
                    grid: {
                        left: '2%',
                        right: '6%',
                        bottom: '3%',
                        containLabel: true
                    },
                    toolbox: {
                        show: false,
                        feature: {
                            saveAsImage: {}
                        }
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: true,
                        name: '学期',
                        data: ['高一上（期末）', '高一下（期末）', '高二上（期末）', '高二下（期末）', '高三上（期末）']
                    },
                    yAxis: {
                        type: 'value',
                        max: 700,
                        splitNumber: 7,
                    },
                    series: [
                        {
                            name: '成绩',
                            type: 'line',
                            data: data1,
                        },
                        {
                            name: '排名',
                            type: 'line',
                            data: data2,
                        },
                    ]
                };

                // 使用刚指定的配置项和数据显示图表。
                myChart.setOption(option);
            },
        },
        created(){
            this.getGoalsOverview();
            this.gaokaoList();
            this.xueqiList();
            this.jinqiList(1,this.pageSize);
            this.xueKaoList();
            this.jinqiFurtherWayList();
        }
    }
</script>
